<template>
  <div>
    <div class="input-group mb-3">
      <input @keypress.enter="searchVideos(query)" v-model="query" type="text" class="form-control" placeholder="youtube 동영상 검색하기" aria-label="youtube 동영상 검색하기" aria-describedby="searchBtn">
      <button @click="searchVideos(query)" class="btn btn-outline-secondary" type="button" id="searchBtn">search</button>
    </div>
  </div>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  name: 'SearchBar',
  data() {
    return {
      query : '',
    }
  },
  methods: {
    ...mapActions([
      'searchVideos'
    ])
  }
}
</script>

