<template>
  <div>
    <ul class="list-group">
      <VideoListItem v-for="(video, idx) in videoList" :key="idx" :video="video" />
    </ul>
    <div v-show="isPageAvailable" class="d-flex justify-content-end mt-3"><Pagination/></div>
  </div>
</template>

<script>
import VideoListItem from '@/components/VideoListItem.vue'
import Pagination from '@/components/Pagination.vue'
import { mapState,mapGetters } from 'vuex'

export default {
  name: 'VideoList',
  components: {
    VideoListItem,
    Pagination,
  },
  computed: {
    ...mapState([
      'videoList'
    ]),
    ...mapGetters(
      ['isPageAvailable']
    )
  }
}
</script>

