import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import _ from 'lodash'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    videoList: [],
    videoDetail: {},
    URL: 'https://www.googleapis.com/youtube/v3/search',
    KEY: 'AIzaSyAxMFWcJQCAB3X1zHYLMJ5nHcvup8z561g',
    nextPageToken: '',
    prevPageToken: '',
    req: {},
  },
  getters: {
    isDetailAvailable(state) {
      return !!Object.keys(state.videoDetail).length
    },
    isPageAvailable(state) {
      return state.nextPageToken||state.prevPageToken
    },
    videoTitle(state) {
      return _.unescape(state.videoDetail.snippet.title)
    }, 
  },
  mutations: {
    SEARCH_VIDEOS(state, videos){
      state.videoList = videos.map(video => {
        video.snippet.title = _.unescape(video.snippet.title)
        video.snippet.description = _.unescape(video.snippet.description)
        return video
      })
    },
    VIDEO_DETAIL(state, videoObj){
      state.videoDetail = videoObj
    },
    NEXT_PAGE_TOKEN(state, page){
      state.nextPageToken = page
    },
    PREV_PAGE_TOKEN(state, page){
      state.prevPageToken = page
    },
    SAVE_CURR_REQUEST(state, req){
      state.req = req
    },
  },
  actions: {
    sendRequest({commit}, req){
      axios(req)
      .then(res => {
        console.log('response:', res.data)
        commit('SEARCH_VIDEOS', res.data.items)
        
        if (res.data.nextPageToken){
          commit('NEXT_PAGE_TOKEN', res.data.nextPageToken)
        }

        if (res.data.prevPageToken){
          commit('PREV_PAGE_TOKEN', res.data.prevPageToken)
        }
      })
      .catch(err => {
        console.log(err)
      })
    },
    searchVideos({commit, dispatch, state}, query) {
      const req = {
        method: 'get',
        url: state.URL,
        params: {
          key: state.KEY,
          part: 'snippet',
          type: 'video',
          q: query,
          maxResults: 5,
        }
      }
      commit('SAVE_CURR_REQUEST', req)
      dispatch('sendRequest', req)
    },
    getVideoDetail({commit}, videoObj) {
      commit('VIDEO_DETAIL', videoObj)
    },
    goTo({dispatch, state}, pageToken) {
      const req = {
        ...state.req,
        params: {
          ...state.req.params,
          pageToken
        }
      }
      dispatch('sendRequest', req)
    }
  },
  modules: {
  }
})
