<template>
  <nav aria-label="Page navigation example">
  <ul class="pagination">
    <li v-if="prevPageToken" class="page-item"><a class="page-link" @click="goTo(prevPageToken)">Previous</a></li>
    <li v-if="nextPageToken" class="page-item"><a class="page-link" @click="goTo(nextPageToken)">Next</a></li>
  </ul>
</nav>
</template>

<script>
import {mapState} from 'vuex'
import {mapActions} from 'vuex'

export default {
  name: 'Pagination',
  methods: {
    ...mapActions([
      'goTo'
    ])
  },
  computed: {
    ...mapState([
      'nextPageToken',
      'prevPageToken'
    ])
  }
}
</script>

