<template>
  <div class="bg-light">
    <div class="mb-3 border-bottom h3">
      {{ videoTitle }}
    </div>
    <div class="ratio ratio-16x9 mb-3">
      <iframe width="560" height="315" :src="videoDetailURL" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
    </div>
    <div class="border mb-3">
      {{ videoOverview }}
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'VideoDetail',
  computed: {
    ...mapState(['videoDetail']),
    videoTitle() {
      return this.videoDetail.snippet.title
    },
    videoDetailURL() {
      return `https://www.youtube.com/embed/${this.videoDetail.id.videoId}`
    },
    videoOverview() {
      return  this.videoDetail.snippet.description
    },
  }
}
</script>

