<template>
  <li @click="getVideoDetail(video)" class="list-group-item list-group-flush list-group-item-action d-flex">
    <div>
    <img :src="videoImg" alt="thumbnail">
    </div>
    <p class="title">{{ videoTitle }}</p>
  </li>
</template>

<script>
import {mapActions} from 'vuex'

export default {
  name: 'VideoListItem',
  props: {
    video: Object,
  },
  methods: {
    ...mapActions([
      'getVideoDetail'
    ])
  },
  computed: {
    videoImg() {
      return this.video.snippet.thumbnails.default.url
    },
    videoTitle() {
      return this.video.snippet.title
    },
  }
}
</script>

<style>
  .title{
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
</style>